import React from 'react';
import "./Habilidades.css"

function Nivel(){
    return 
}


class Habilidade extends React.Component{

    render(){
        return(
            <div className="habilidade-container">

                {/*Nome da habilidade*/}
                <div className="margem-habilidade">{this.props.nome}</div>

                {/*a barrinha em si*/}
                <div className="barrinha-container">

                    {/*Parte preenchida*/}
                    <div 
                    className="preenchimento-barrinha"
                    style={{flex:this.props.porcentagem}}/>

                    {/*Parte que falta*/}
                    <div style={{flex:100  -  this.props.porcentagem}}/>              
                </div>
            </div>
        )
    }
}

export default Habilidade;